import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import Markdown from 'preact-markdown';

import ImageSlider from '../imageSlider';
import StoryComments from './comments';
import StoryAddComment from './commentAdd';

import mu from '../../lib/musher-util';

const initialState = {
	showAddComment: {},
};

@observer
class StoryPublicFeed extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.inputRef = {};
    }

    toggleDate = (e) => {
        e.stopPropagation();
        e.preventDefault();
        const { showDate } = this.state;
        this.setState({ showDate: !showDate });
    }

	likeStory = async (e) => {
        const storyId = parseInt(e.target.closest('button').dataset.id, 10);

		const { storyStore } = this.props.stores;
		const response = await storyStore.likeStory({ id: storyId });
        if (response.status === 200) {
            // this.addLikeToFeed(storyId, response.data);
		}
	}

	toggleAddComment = (e) => {
		const storyId = e.target.closest('button').dataset.id;
		const { showAddComment } = this.state;
        if (showAddComment[storyId]) {
            delete showAddComment[storyId];
        } else {
            showAddComment[storyId] = true;
        }
		this.setState({
			showAddComment,
		}, () => {
			if (this.inputRef[storyId]) {
				this.inputRef[storyId].focus();
			}
		});
	}

    async loadStoriesPublicFeed(props = this.props) {
		const { storyStore } = props.stores;
		await storyStore.loadPublicFeed();
	}

	componentDidMount() {
        this.loadStoriesPublicFeed();
	}

    render() {
        const { showAddComment, showDate } = this.state;
        const { userStore, storyStore } = this.props.stores;
        const { user } = userStore;
        const { publicFeed } = storyStore;
		const { language = 'no' } = user;

        return (
            <div class='w-100 mb-3'>
                {publicFeed && publicFeed.map((story) => {
                    const team = storyStore.findPublicTeam(story.team) || {};
                    let image;
                    if (team.image && team.image.s3SmallLink) {
                        image = team.image;
                    } else if (team.images && team.images[0] && team.images[0].s3SmallLink) {
                        image = team.images[0];
                    }
                    const { likes, comments } = story;
                    const hasLikedStory = story.likes && story.likes.findIndex(e => e.user === user.id) > -1;

                    return (<>
                        <div class='row pb-4'>
                            <div class='col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 mb-0 position-relative bg-light rounded rounded-lg py-3'>

                                <div class='d-flex'>
                                    <div class={`text-center border rounded-circle imageRounded`} style={image ? `background-image: url("${image.s3SmallLink}");` : ''}>
                                        {!image && <i class='fas fa-users text-muted mt-3' style='font-size: 40px;' />}
                                    </div>
                                    <div class='flex-grow-1 pl-3' style='line-height: 1.2em;'>
                                        <h5 class='mb-1' style='line-height: 1.0em;'>{team.name}</h5>
                                        <div>
                                            {story.date && <small class='text-muted' onClick={this.toggleDate}>
                                                {showDate ? util.formatDate(story.date, { locale: 'no-NB', hour12: false, hour: '2-digit', minute: '2-digit' }) : util.formatDistance(story.date, new Date(), { locale: 'no-NB' })}
                                            </small>}
                                        </div>

                                        <Markdown markdown={mu.replaceImages(story.body, story.images, language)} markdownOpts={mu.getMarkdownOptions()} />
                                    </div>
                                </div>
                                <div>
                                    <div class='mx-0 px-0 clearfix row'>
                                        {story.images && story.images.map((img, idx) => {
                                            const width_first = 'col-12';
                                            const width_second = 'col';
                                            return (<>
                                                <div class={`px-0 ${idx === 0 ? width_first : width_second}`}>
                                                    <img class={`img-fluid ${idx > 0 ? 'border border-2' : ''}`} style='min-width: 100px;' src={img.s3LargeLink} />                                        
                                                </div>
                                            </>);
                                        })}
                                    </div>
                                </div>
                                <div class='mt-2'>
                                    {likes && likes.length > 0 && <small class='bg-light shadow-sm rounded-lg py-0 px-1' style='bottom: 20px; right: 15px;'>
                                        <i class='fas fa-thumbs-up text-primary' /> {likes.length}
                                    </small>}
                                    {comments && comments.length > 0 && <small class='float-right bg-light shadow-sm rounded-lg py-0 px-1' style='bottom: 20px; right: 15px;'>
                                        <i class='fas fa-comment text-primary' /> {comments.length}
                                    </small>}
                                </div>
                                <div>
                                    <div class='row'>
                                        <div class='col-6 text-center'>
                                            <button type='button' class={`btn btn-sm btn-outline-${hasLikedStory ? 'primary' : 'secondary'} btn-lg btn-block ${hasLikedStory ? 'text-primary' : ''}`}
                                                data-id={story.id}
                                                onClick={this.likeStory}
                                                // disabled={hasLikedStory}
                                            >
                                                <i class={`${hasLikedStory ? 'text-primary fas' : 'far'} fa-thumbs-up`} /> <Text id='comments.like'>Lik</Text>
                                            </button>
                                        </div>
                                        <div class='col-6 text-center'>
                                            <button type='button' class='btn btn-sm btn-outline-secondary btn-lg btn-block'
                                                data-id={story.id}
                                                onClick={this.toggleAddComment}
                                            >
                                                <i class='fas fa-comment' /> <Text id='comments.comment-add'>Kommentar</Text>
                                            </button>
                                        </div>
                                    </div>
                                </div>
                                <div class='mt-2'>
                                    {showAddComment[story.id] && <StoryAddComment
                                        stores={this.props.stores}
                                        story={story}
                                        inputRef={c => this.inputRef[story.id] = c}
                                        col='10'
                                        offset='2'
                                    />}
                                    <StoryComments
                                        stores={this.props.stores}
                                        story={story}
                                        col='10'
                                        offset='2'
                                        compact={false}
                                    />
                                </div>

                            </div>

                        </div>
                    </>);
                })}
            </div>
        );
    }
}

export default StoryPublicFeed;
